import request from '@/static/request.js'
export default {
  data() {
    return {
      id: '100',
      telephone: '',
      correctAuthcode: '1234567890',
      authcode: '',
    }
  },
  methods: {
    checkAuthcode() {
      const vm = this;
      request
        .get('getId/', {
          user_telephone: vm.telephone,
        })
        .then(res => {
          vm.id = res.data.user_id;
          if(vm.authcode === vm.correctAuthcode) {
            uni.setStorage({
              key: 'GetId',
              data: vm.id
            });
            uni.reLaunch({
              url: '../user/user'
            });
          }
          else {
            uni.showToast({
              title: '验证码错误！',
              duration: 1000,
            })
          }
        })
    },
    sendAuthcode() {
      const vm = this;
      request
        .get('weixinLogin/', {
          user_telephone: vm.telephone,
        })
        .then(res => {
          if(res.data.auth_code === '000000') {
            uni.showToast({
              title: '请先注册新用户！',
              duration: 1000,
            })
          }
          else if(res.data.statuscode === '400') {
            uni.showToast({
              title: '发送失败！',
              duration: 1000,
            })
          }
          else {
            uni.showToast({
              title: res.data.auth_code,
              duration: 1000,
            })
            vm.correctAuthcode = res.data.auth_code;
          }
        })
    }
  }
};
